"use client";

import Image from "next/image";
import { theme } from "@/lib/config/theme";
import { usePassenger } from "@/lib/context/PassengerContext";
import { Plane } from "lucide-react";

interface HeroSectionProps {
    onOpenClassSelector?: () => void;
}

export default function HeroSection({ onOpenClassSelector }: HeroSectionProps) {
    const { profile } = usePassenger();
    const time = new Date().getHours();
    // Simple greeting logic
    let greeting = "Bonjour";
    if (time >= 18) greeting = "Bonsoir";

    return (
        <div
            className="w-full px-6 pt-10 pb-6"
            style={{
                backgroundColor: theme.colors.background.default,
            }}
        >
            <div className="flex justify-between items-start mb-8">
                <div>
                    <Image
                        src="/icons/logo.png"
                        alt="Logo"
                        width={60}
                        height={60}
                        className="mb-4"
                    />
                    <h1
                        className="font-bold mb-1"
                        style={{
                            fontSize: "32px",
                            color: theme.colors.text.primary,
                            lineHeight: 1.1,
                        }}
                    >
                        {greeting},
                    </h1>
                    <p
                        style={{
                            fontSize: theme.typography.base.fontSize,
                            color: theme.colors.text.secondary,
                        }}
                    >
                        Pr√™t pour votre voyage ?
                    </p>
                </div>
            </div>

            {/* Flat Status Card */}
            {profile ? (
                <div
                    className="w-full p-5 rounded-none"
                    style={{
                        backgroundColor: theme.colors.gray[100],
                        borderLeft: `4px solid ${theme.colors.accent.main}`,
                    }}
                    onClick={onOpenClassSelector} // Allow changing class by clicking card too
                >
                    <div className="flex items-center gap-3 mb-2">
                        <Plane className="w-5 h-5" style={{ color: theme.colors.accent.main }} />
                        <span
                            className="font-bold text-lg"
                            style={{ color: theme.colors.text.primary }}
                        >
                            Vol {profile.flightNumber}
                        </span>
                    </div>
                    <div className="flex justify-between items-end">
                        <div className="text-sm" style={{ color: theme.colors.text.secondary }}>
                            {profile.gate ? (
                                <span>Porte <strong style={{ color: theme.colors.text.primary }}>{profile.gate}</strong></span>
                            ) : (
                                "Porte non annonc√©e"
                            )}
                        </div>
                        <div
                            className="px-3 py-1 text-xs font-bold uppercase"
                            style={{
                                backgroundColor: theme.colors.accent.main,
                                color: "#FFF",
                            }}
                        >
                            {profile.class === 'economy' ? 'ECO' : profile.class === 'business' ? 'BUS' : 'PREM'}
                        </div>
                    </div>
                </div>
            ) : (
                <div
                    className="w-full p-5 cursor-pointer transition-opacity active:opacity-80"
                    style={{
                        backgroundColor: theme.colors.accent.main,
                        color: "#FFF",
                    }}
                    onClick={onOpenClassSelector}
                >
                    <h3 className="font-bold text-lg mb-1">Bienvenue √† Lom√©</h3>
                    <p className="text-sm opacity-90">S√©lectionnez votre classe pour commencer</p>
                </div>
            )}
        </div>
    );
}
