"use client";

import { usePassenger } from "@/lib/context/PassengerContext";
import { getApplicableSteps } from "@/lib/types/passenger";
import { theme } from "@/lib/config/theme";
import { Check, Circle } from "lucide-react";
import Link from "next/link";

export default function JourneyTimeline() {
    const { profile } = usePassenger();

    if (!profile) return null;

    const steps = getApplicableSteps(profile.class);
    const currentStepIndex = profile.currentStep || 0;

    return (
        <div className="px-6 mb-10">
            <h2
                className="font-bold mb-4 text-lg"
                style={{ color: theme.colors.text.primary }}
            >
                Votre Parcours
            </h2>

            <div className="relative border-l-2 ml-3" style={{ borderColor: theme.colors.gray[200] }}>
                {steps.map((step, index) => {
                    const isCompleted = index < currentStepIndex;
                    const isCurrent = index === currentStepIndex;

                    return (
                        <div key={step.id} className="mb-8 pl-6 relative">
                            {/* Dot Indicator */}
                            <div
                                className="absolute -left-[9px] top-1 w-4 h-4 rounded-full flex items-center justify-center"
                                style={{
                                    backgroundColor: isCompleted || isCurrent
                                        ? theme.colors.accent.main
                                        : theme.colors.background.default,
                                    border: `2px solid ${isCompleted || isCurrent
                                            ? theme.colors.accent.main
                                            : theme.colors.gray[300]
                                        }`,
                                }}
                            >
                                {isCompleted && <Check className="w-2 h-2 text-white" strokeWidth={4} />}
                            </div>

                            {/* Content */}
                            <div>
                                <h3
                                    className={`font-bold text-base mb-1 ${isCurrent ? 'text-gray-900' : 'text-gray-500'}`}
                                >
                                    {step.title}
                                </h3>
                                <p className="text-sm text-gray-500 mb-2">
                                    {step.description}
                                </p>

                                {isCurrent && (
                                    <Link
                                        href="/navigation"
                                        className="inline-block px-4 py-2 mt-2 text-sm font-bold text-white transition-opacity active:opacity-80"
                                        style={{ backgroundColor: theme.colors.accent.main }}
                                    >
                                        Y aller maintenant
                                    </Link>
                                )}
                            </div>
                        </div>
                    );
                })}
            </div>
        </div>
    );
}
