"use client";

import Link from "next/link";
import { theme } from "@/lib/config/theme";
import { MapPin, Navigation, Info, AlertTriangle } from "lucide-react";

const actions = [
    {
        href: "/map",
        icon: MapPin,
        title: "Plan",
        color: theme.colors.info.main,
    },
    {
        href: "/navigation",
        icon: Navigation,
        title: "Naviguer",
        color: theme.colors.accent.main,
    },
    {
        href: "/services",
        icon: Info,
        title: "Services",
        color: theme.colors.success.main,
    },
    {
        href: "/report",
        icon: AlertTriangle,
        title: "Alerte",
        color: theme.colors.error.main,
    },
];

export default function QuickActionsGrid() {
    return (
        <div className="px-6 mb-8">
            <h2
                className="font-bold mb-4 text-lg"
                style={{ color: theme.colors.text.primary }}
            >
                Acc√®s Rapide
            </h2>
            <div className="grid grid-cols-2 gap-3">
                {actions.map((action) => {
                    const Icon = action.icon;
                    return (
                        <Link
                            key={action.title}
                            href={action.href}
                            className="flex flex-col items-center justify-center p-4 transition-colors active:opacity-80" // Touch feedback
                            style={{
                                backgroundColor: theme.colors.gray[100], // Flat background
                                height: "100px",
                            }}
                        >
                            <Icon className="w-8 h-8 mb-2" style={{ color: action.color }} />
                            <span
                                className="font-semibold text-sm"
                                style={{ color: theme.colors.text.primary }}
                            >
                                {action.title}
                            </span>
                        </Link>
                    );
                })}
            </div>
        </div>
    );
}
