"use client";

import { useState, useEffect } from "react";
import { usePassenger } from "@/lib/context/PassengerContext";
import { PassengerClass } from "@/lib/types/passenger";
import { theme } from "@/lib/config/theme";
import ClassSelectorModal from "@/components/ui/ClassSelectorModal";
import OnboardingTutorial from "@/components/ui/OnboardingTutorial";
import HeroSection from "@/components/home/HeroSection";
import QuickActionsGrid from "@/components/home/QuickActionsGrid";
import JourneyTimeline from "@/components/home/JourneyTimeline";

export default function Home() {
  const { profile, setProfile } = usePassenger();
  const [showModal, setShowModal] = useState(false);
  const [showOnboarding, setShowOnboarding] = useState(false);

  useEffect(() => {
    // Show modal on first load if no profile
    if (!profile) {
      setShowModal(true);
    }
  }, [profile]);

  const handleClassSelect = (
    selectedClass: PassengerClass,
    flightNumber?: string,
    gate?: string
  ) => {
    setProfile({
      class: selectedClass,
      flightNumber,
      gate,
      terminal: "1",
    });
    setShowModal(false);
  };

  const handleFirstTimeSelect = () => {
    const hasSeenOnboarding = localStorage.getItem("hasSeenOnboarding");
    if (!hasSeenOnboarding) {
      setTimeout(() => {
        setShowOnboarding(true);
      }, 300);
    }
  };

  const handleOnboardingComplete = () => {
    setShowOnboarding(false);
    localStorage.setItem("hasSeenOnboarding", "true");
  };

  return (
    <>
      <ClassSelectorModal
        isOpen={showModal}
        onClose={() => setShowModal(false)}
        onSelect={handleClassSelect}
        onFirstTimeSelect={handleFirstTimeSelect}
      />

      <OnboardingTutorial
        show={showOnboarding}
        onComplete={handleOnboardingComplete}
      />

      <div
        className="min-h-screen pb-20"
        style={{
          backgroundColor: theme.colors.background.default,
        }}
      >
        <HeroSection onOpenClassSelector={() => setShowModal(true)} />

        <QuickActionsGrid />

        <JourneyTimeline />
      </div>
    </>
  );
}
